<script>
  import util from '../../js/util'
  import loading from '../../plugins/loading/loading';
//  import {mapGetters} from 'vuex';
  import page from '@/plugins/page';
//  import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
  import Choosen from '../biz/compositeChoosen.vue';
  export default {
    name: 'homePage',
    data() {
       var validateNUll = (rule, value, callback) => {
        if (value.trim() == '') {
          callback(new Error('请填写意见！'));
        } else {
          callback();
        }
       };
       return {
        isShow:true,
        showList: true, //列表状态
        isBacklog:false, //是否显示暂无待办
        showDisagree: false, // 不同意弹窗显示&隐藏
        messageis:'',
        ruleForm: {
          desc: '',//意见
        },
        rules: {
          desc: [
            { validator: validateNUll, trigger: 'blur',}
          ]
        },
        /*
        * 使用最多流程数据*/
        toListsCollect: [],
        centerDialogVisible: false,
        feedbackdialog:false,//待反馈弹窗
        showNone:false,
        showTalk:false,//取消沟通
        proid:"",//procInstID
        mians:{},//后台返回参数
        url:'',
        flag:'',//函数判断调用
        opinionList:[],
        mainList: [],//代办列表数据展示
        toListsCont:[], //收藏数据渲染
        colectPageIndex: 1,
        colectPageSize: 10,
        colectTotal: 0,
        CriteriaModel:{
          folio:'',//单号
          ProcessCode:'', //流程名称
          formSubject:'', //主题
          originator:'', //申请人
          startDate:'', //开始时间
          endDate:'',  //结束时间
          approvers:'', //当前审批人
          sortfield:'', //排序字段
          sortdir:'', //排序方式
          status:'', //流程状态
          currentPage:1,
          total:0,
          pageSizesList: [8,20],
          PageIndex:1, //当前页码
          PageSize:8 //每页数量
        },//获取我的代办初始参数定义
        loadMore:true,
        rejectDot:'',//回退节点
        rejectDotData:[],
        isDot:false,
        isrRevisit:true,
        isDotShow:false,
        portalmMtilselectCheck:'false',//单选多选配置
        ProcessType:'',//流程类型，用于父组件给子组件
        UserType:'',//选人类型
        typeTab:'User',
        Visible:false,
        CommunicationData:[],//沟通数据
        tagsOwnsUser:[],
        showDown:false,
      }
    },
    components: {
      page,
      Choosen
    },
    mounted() {
      var _this = this;
      _this.$nextTick(()=>{
        _this.getAllList('true');
      })
    },
    methods: {
      buttonSet(v,i){
        this.mians = i;
        if(v.Code ==='Approve'){
          this.agreeDialog(v,i)
        }else if(v.Code==='Returned'){
          this.reject('reject',i)
        }else if(v.Code==='Communication'){
          this.choosenDialogType('User','userSelectFell')
        }else if(v.Code==="CancelCommunicate"){
          this.noTalkDialog(v,i)
        }
        else {
          this.feedback('feedback',i)
        }
      },
      /**
       * 埋点事件
       */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      /**
      * 请求代办列表数据
      */
      todolist() {
        var that = this;
        return new Promise((resolve, reject) => {
          that.$axios.post('/api/getMainInfo', {}).then((result) => {
            resolve(result);
          }).catch(err=>{
            reject(`获取数据失败!ERROR MESSAGE:${err}`);
          })
        })
      },
      /**
        * 使用最多的流程数据请求
        */
      getTopProcess(){
        var that = this;
        return new Promise((resolve, reject) => {
          that.$axios.post('/api/getTopProcess', {}).then((result) => {
             resolve(result);
          } ).catch(err => {
            reject(`获取数据失败!ERROR MESSAGE:${err}`);
          })
        })
      },
      // 同意弹窗
      agreeDialog(v,item){
        loading.startLoading();
        this.showNone = true;
        this.ruleForm.desc = "";
        this.flag ="approval";
        this.mians = item;
        loading.endLoading()
      },
      //取消沟通
      noTalkDialog(v,item){
        loading.startLoading();
        this.showTalk = true;
        this.ruleForm.desc = "";
        this.flag ="CancelCommunicate";
        this.mians = item;
        loading.endLoading()
      },
      // 不同意弹窗显示&隐藏
      disagreeDialog(item) {
        util.burialPoint('待办列表-不同意');
        loading.startLoading();
        this.showDisagree = true;
        this.mians = item;
        loading.endLoading()
      },
      //校验结果重置
      resetForm(formName) {
        if (formName==='disagreeForm') {
          util.burialPoint('待办列表-不同意-取消');
        }
        this.$refs[formName].resetFields();
        this.centerDialogVisible=false; //关闭退回弹窗
        this.feedbackdialog= false; //待反馈弹窗
        this.showDisagree = false; // 不同意弹窗
        this.showNone = false;  //同意弹窗
        this.showTalk = false;//取消沟通
        this.opinionList=[];//清除意见列表
        this.mians ={}
      },
      //输入框禁止为空
      submitForm(formName) {
        let _this = this;
        _this.$refs[formName].validate((valid) => {
          if (valid) {
            //退回
            if(_this.flag === "reject"){
              _this.close("reject",_this.mians)
            }
            else if(_this.flag === "feedback"){
              _this.CommunicateFeedback(_this.mians);
            }
            else if(_this.flag === 'approval') {
              _this.agree()
            }else if(_this.flag ==='CancelCommunicate'){
              _this.CancelCommunicate(_this.mians)
            }
          }
          else{
            _this.ruleForm.desc = "";
            return false;
          }
        });
      },
      /*
      * 退回弹窗*/
      reject(type,item){
        let _this = this;
        util.burialPoint('待办列表-退回');
        loading.startLoading();
        _this.centerDialogVisible=true;
        _this.ruleForm.desc = "";
        _this.flag = type;
        _this.mians = item;
        _this.getRejectDot(_this.mians);
        loading.endLoading()
      },
      /*
      * 待反馈弹窗*/
      feedback(type,item){
        let _this = this;
        loading.startLoading();
        _this.feedbackdialog=true;
//        _this.ruleForm.desc ="";
        _this.mians = item;
        _this.flag = type;
//        this.select(item);
        loading.endLoading()
      },

      /*
      * 关闭弹窗*/
      close(type,row){
        let _this = this;
        //退回
        if(type =="reject"){
          if(_this.rejectDot===''){
            _this.$message.warning("请选择退回节点");
            return
          }
            let data = {
              sharedUser:row.SharedUser,
              workItemId:row.Id,
              comment:_this.ruleForm.desc,
              activityName:_this.rejectDot,
              isRejectNotGoBack:_this.isrRevisit,
            };
          _this.$axios({
                url:'/Portal/WorkFlow/PortalReject',
                method:'post',
                data:data
              }).then(result => {
                if(result.data.code === 200){
                  _this.$store.dispatch('GETTODO');
                  _this.$message.success("退回成功");
                }else{
                  _this.$message.warning("退回失败");
                }

                _this.getAllList('true')
              }).catch(err => {
                loading.endLoading();
                _this.$message.error(`获取数据失败!ERROR MESSAGE:${err}`);
              });
          }
        _this.mians ={};
        _this.centerDialogVisible = false;
        _this.ruleForm.desc = ''
      },
      /*
      * 同意
      */
      agree(val){
        this.showNone=false;
        this.showDisagree=false;
          let data = {
            sharedUser:this.mians.SharedUser,
            workItemId:this.mians.Id,
            comment:this.ruleForm.desc
          };
          this.$axios.post('/Portal/WorkFlow/PortalApprove', data).then(result => {
            if(result.data.code ===200){
              this.$store.dispatch('GETTODO');
              this.$message.success("审批通过");
            }else{
              this.$message.warning("审批不通过");
            }
            this.getAllList('true');
          }).catch(err => {
            loading.endLoading();
            this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
          });
        this.showNone = false;
        this.mians ={}
      },
      //取消沟通
      CancelCommunicate(row){
        loading.startLoading();
        let data = {
          sharedUser:row.SharedUser,
          workItemId:row.Id,
          processNum:row.Folio,
          comment:this.ruleForm.desc
        };
        this.$axios({
          url:'/Portal/WorkFlow/PortalCancelCommunicate',
          method:'post',
          data:data
        }).then(res=>{
          if(res.data.code===200){
            this.$store.dispatch('GETTODO');
            this.$message.success("取消沟通成功");
            this.getAllList('true');
          }
        }).catch(err=>{
          this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        });
        this.showTalk=false;
        loading.endLoading();
        this.mians ={}
      },
      //沟通反馈
      CommunicateFeedback(row){
        let _this = this;
        let data = {
          sharedUser:row.SharedUser,
          workItemId:row.Id,
          processNum:row.Folio,
          comment:_this.ruleForm.desc
        };
        _this.$axios({
          url:'/Portal/WorkFlow/PortalCommunicateFeedback',
          method:'post',
          data:data
        }).then(res=>{
          if(res.data.code===200) {
            _this.$store.dispatch('GETTODO');
            _this.$message.success("沟通反馈成功");
            _this.getAllList('true');
            _this.feedbackdialog = false;
          }
        }).catch(err=>{
          this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        })
      },
      //获取全部数据
      getAllList(val){
        let _this = this;
        _this.ruleForm.desc = '';
        loading.startLoading();
        Promise.all([_this.getTodoList(),_this.getListCollect(val)]).then(result => {
          if(result[0]){
            _this.isBacklog = false;
            _this.mainList = result[0].data.data.item;
            _this.CriteriaModel.total = result[0].data.data.total;
          }
          if(result[1]){
            _this.isBacklog = false;
            _this.toListsCont = result[1].data.data.items
            _this.colectTotal = result[1].data.data.count
          }
          loading.endLoading();
        }).catch(error => {
          loading.endLoading();
          this.$message.error(`获取数据失败!ERROR MESSAGE:${error.message}`);
        })
      },
      //获取我的收藏接口
      getListCollect(val){
        let data = {
          pageIndex:this.colectPageIndex,
          pageSize:this.colectPageSize,
        };
        let _this = this
        return new Promise((resolve,reject)=>{
          _this.$axios({
            url:'Portal/WorkflowMap/GetMyFavoriteWorkflow',
            method:'post',
            data:data
          }).then(res=>{
            if(res.data.code===200) {
              resolve(res)
            }
          }).catch(err=>{
            reject(err)
          })
      })
        // if(val==='true'){
        //   //默认处理10条数据
        //   let _this = this;
        //   return new Promise((resolve,reject)=>{
        //     _this.$axios({
        //       url:'',
        //       method:'GET',

        //     }).then(res=>{
        //       if(res.data.code===200) {
        //         let list = [];
        //         if(res.data.data.length>10){
        //           list = [...res.data.data.splice(0,10)];
        //         }else {
        //           list = [...res.data.data.splice(0,res.data.data.length)];
        //         };
        //         resolve(list)
        //       }
        //     }).catch(err=>{
        //       reject(err)
        //     })
        //   });
        // }
        // else {
        //   //加载全部数据
        //   let _this = this;
        //   return new Promise((resolve,reject)=>{
        //     _this.$axios({
        //       url:'Portal/WorkflowMap/GetMyFavoriteWorkflow',
        //       method:'GET',
        //     }).then(res=>{
        //       if(res.data.code===200) {
        //         resolve(res.data.data)
        //       }
        //     }).catch(err=>{
        //       reject(err)
        //     })
        //   });
        // }
      },
      //获取代办列表
      getTodoList(){
        let _this = this;
        let data = {
          pageIndex:_this.CriteriaModel.PageIndex,
          pageSize:_this.CriteriaModel.PageSize,
        };
        return new Promise((resolve,reject)=>{
          _this.$axios({
            url:'Portal/WorkFlow/GetWorklist',
            method:'post',
            data:data
          }).then(res=>{
            if(res.data.code===200) {
              resolve(res)
            }
          }).catch(err=>{
            reject(err)
          })
      })
      },
      //点击 每页显示条目数
      handleSizeChange(val) {
        var _this =this;
        var pageSize =  val;
        _this.CriteriaModel.PageIndex = 1;
        _this.CriteriaModel.PageSize = parseInt(pageSize);
        _this.getTodoList().then(result=>{
          _this.isBacklog = false;
          _this.mainList = result.data.data.item;
        }).catch(err=>{

        });
      },
      //点击页码
      handleCurrentChange(val) {
        var _this =this;
        _this.CriteriaModel.PageIndex = val;
        _this.getTodoList().then(result=>{
          _this.isBacklog = false;
          _this.mainList = result.data.data.item;
        }).catch(err=>{

        });
      },
      //加载更多获取数据
      loadMoreList(){
        this.colectPageSize += 10
        let _this = this
        this.getListCollect().then(res => {
            _this.isBacklog = false;
            _this.toListsCont = res.data.data.items
        })
      //  let _this = this;
      //  _this.showDown = !_this.showDown;
      //  _this.loadMore = !_this.loadMore;
      //  if(_this.loadMore){
      //    _this.getAllList('true')
      //  }else{
      //    _this.getAllList('false')
      //  }
      },
      //沟通
      Communication(val,row){
        this.CommunicationData = [];
        for(let item of val){
          this.CommunicationData.push(
            {
              UserAccount:item.UserAccount,
              UserDisplayName:item.UserDisplayName
            }
            )
        };
        let data = {
          sharedUser:row.SharedUser,
          workItemId:row.Id,
          processNum:row.Folio,
          comment:'',
          toUser:this.CommunicationData
        };
        this.$axios({
          url:'Portal/WorkFlow/PortalCommunication',
          method:'post',
          data:data
        }).then(res=>{
        if(res.data.code === 200){
          this.$message.success("成功沟通");
          this.getAllList('true');
        }
        }).catch(err=>{
          this.$message.error("沟通失败");
        });
      },
      //获取回退节点数据
      getRejectDot(row){
        let _this = this;
        let data = {
          sharedUser:row.SharedUser,
          workItemId:row.Id
        };
        _this.$axios({
          url:'Portal/WorkFlow/GetPortalRejectData',
          method:'post',
          data:data
        }).then(res=>{
          if(res.data.code===200){
            _this.rejectDotData = [];
            _this.isDotShoW = res.data.data.IsRejectNotGoBack;
            _this.isDot =  res.data.data.IsRejectNotGoBack;
            for(let item of res.data.data.BackActivitys){
              _this.rejectDotData.push({
                label:item.ActivityDisplayName,
                value:item.ActivityName
              });
              this.rejectDot = this.rejectDotData[0].label;
            }
          }
        }).catch(err=>{
          this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        })
      },
      //删除收藏
      delItem(row){
      let _this = this;
      let index = _this.toListsCont.indexOf(row);
      let data  = {
        processCode:row.ProcessCode
      };
      _this.$axios({
        url:'/Portal/WorkflowMap/DelWorkflowFavorite',
        method:'post',
        data:data,
      }).then(res=>{
        if(res.data.code===200){
          setTimeout(()=>{
            this.$message.success("取消收藏");
          },2000);
//          _this.getAllList('true');
          _this.toListsCont.splice(index,1);
        }
      }).catch(err=>{
        _this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        _this.getAllList('true')
      });
      },

      choosenDialogType(val,type){
        var that = this;
        that.typeTab = val;
        //打开流程弹窗
        if(that.ProcessType &&(that.ProcessType==='Category' || that.ProcessType==='Process')){
          that.ProcessType = type;
        }else {
         this.UserType = type;
        }
        that.$refs.dialogChoosen.openChoosen(that.typeTab,type);
        that.$refs.dialogChoosen.getCollect();
      },
      //打开弹窗
      OpenDialog(val){
        var that = this;
        that.Visible = val;
      },
      //关闭弹窗
      CloseDialog(val){
        var that = this;
        that.Visible = val;
        that.tagsOwnsUser = [];
        that.$refs.dialogChoosen.tags = [];
      },
      //接收子组件确定按钮带过的数据
      sendTag(val){
        let _this = this;
        _this.tagsOwnsUser = [];
        for(let item of val){
          if(item.type==='User'){
            _this.tagsOwnsUser.push(item);
          }
          }
          //直接调用沟通接口
          _this.Communication(_this.tagsOwnsUser,_this.mians);
          _this.tagsOwnsUser = [];
        },
      //删除页面回显中的tag
      closeProcessTag(item){
        switch (item.type){
          case 'Process':{
            this.tagsOwnProcess.splice(item,1);
            this.$refs.dialogChoosen.portalehco(this.tagsOwnProcess);
            break;
          }
          case 'Category':{
            this.tagsOwnCategory.splice(item,1);
            this.$refs.dialogChoosen.portalehco(this.tagsOwnCategory);
            break;
          }
          case 'User':{
            this.tagsOwnsUser.splice(item,1);
            this.CriteriaModel.Originator = '';
            this.$refs.dialogChoosen.portalehco(this.tagsOwnsUser,null)
          }
        }
      },
      //流程主题跳转
      formBlank(row){
        window.open(row.Url,'_blank');
      },
    },
    //字符转换
    filters:{
      condition:function (dodo) {
        if(dodo == '3'){
            return '待反馈'
        }else {
          return '待办'
        }
      },
      //时间分割
      timeSplit:function (timr) {
        return timr.split(".")[0]
      }
    },

    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight;
      },
//      ...mapGetters(['fullWidth', 'bodyHeight'])
    }
  }
</script>

